@import "../styles/variable.scss";

.outlined-button {
  position: relative;
  display: block;
  color: $font-color-header;
  padding: 3px;
  border: 1px solid $font-color-header;
  border-radius: 5px;
  box-sizing: border-box;
  max-width: 250px;
  cursor: pointer;

  .button {
    position: relative;
    display: block;
    padding: 8px 30px;
    border: 1px solid $font-color-header;
    border-radius: 6px;
    box-sizing: border-box;
    text-align: center;
  }
}
