@import "../styles/variable.scss";

$box-width: 280px;

.mine-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  color: $font-color-highlight;
  line-height: 1;
}

.mine-earning {
  .title {
    margin: 12px 0;
    font-size: 20px;
    text-align: center;
    line-height: 1;
  }

  .desc {
    margin: 16px 0 10px;
    font-size: 12px;
    line-height: 1;
    text-align: center;
  }

  .vice {
    margin: 10px 0;
    color: $font-color-default;
  }
}

.mine-main {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: $font-color-highlight;
  line-height: 1;

  .title {
    margin: 16px 0 0;
    font-size: 16px;
    text-align: center;
    line-height: 1;
  }

  .desc {
    margin: 28px 0 0;
    font-size: 12px;
    line-height: 1;
    text-align: center;
    color: $font-color-default;
  }

  .desc-sub {
    margin: 8px 0 0;
    font-size: 12px;
    line-height: 1;
    text-align: center;
    color: $font-color-default;
    transform: scale(0.8, 0.8);
  }

  .num {
    margin: 16px 0 0;
    font-size: 24px;
    text-align: center;
    line-height: 1;
  }

  .num-top {
    margin-top: 6px;
    font-size: 14px;
    text-align: center;
    line-height: 1;
  }

  .num-sub {
    margin: 15px 0 20px;
    font-size: 12px;
    line-height: 1;
    text-align: center;
  }

  .outlined {
    margin-top: 10px;
    margin-bottom: 20px;
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 5px 8px;
    font-size: 13px;
    border: 1px solid $font-color-highlight;
    border-radius: 3px;
  }
}

.mine-mid {
  min-width: 200px;
  padding: 20px 10px;
  box-sizing: border-box;

  .label-title {
    margin: 16px 0 0;
    font-size: 16px;
    text-align: center;
    line-height: 1;
  }

  .label-desc {
    margin: 10px 0 0;
    font-size: 12px;
    line-height: 1;
    text-align: center;
    color: $font-color-default;
  }
}

.mine-box {
  margin-top: 12px;
  .box {
    width: $box-width;
    height: 250px;
  }
}

.mine-input {
  margin-top: 6px;
  input {
    width: 52%;
  }
  .balance-input {
    width: 250px;
  }
}

.mine-small-button {
  margin: 10px 0;
  .button {
    width: 200px;
    color: $background-color;
    background-color: $font-color-highlight;;
  }
}

.mine-withdraw {
  margin-top: 30px;
  width: 300px;
  border: 1px solid $font-color-highlight;
  border-radius: 5px;

  .mine-withdraw-input {
    margin: 3px auto;
    width: 250px;
    input {
      width: 60%;
    }
    .balance-input {
      margin-top: 6px;
    }
  }

  .mine-withdraw-button {
    margin: 0 0 10px;
    .button {
      width: 240px;
    }
  }

  p {
    margin: 0;
  }
}

.mine-desc {
  padding: 15px 0;
  font-size: 13px;
  line-height: 16px;
}

@media screen and (max-width: 720px) {
  .mine-main {
    flex-direction: column;
  }
  .mine-withdraw {
    width: 270px;
    .mine-withdraw-input {
      width: 240px;
    }
  }
}
